export function adjLongPresentPositive(word) {
    let result = word.hiragana;

    if (word.type === "na-adjective") {
        result = result.replace(/\s\(\)/giu, "");
    } else if (word.type === "i-adjective") {
        result = result + "";
    }

    return result;
}

